import L from "leaflet";
export var VelocityControl = L.Control.extend({
  options: {
    tooltip: true,
    popup: true,
    latLabel: "Lat",
    lngLabel: "Lng",
    speedLabel: "Speed",
    directionLabel: "Direction",
    setPopupContent: null,
    position: "bottomleft",
    emptyString: "Unavailable",
    // Could be any combination of 'bearing' (angle toward which the flow goes) or 'meteo' (angle from which the flow comes)
    // and 'CW' (angle value increases clock-wise) or 'CCW' (angle value increases counter clock-wise)
    angleConvention: "bearingCCW",
    // Could be 'm/s' for meter per second, 'k/h' for kilometer per hour or 'kt' for knots
    speedUnit: "m/s",
    onAdd: null,
    onRemove: null
  },
  initialize: function initialize(leafletVelocity, options) {
    L.setOptions(this, options);
    this._leafletVelocity = leafletVelocity;
  },
  onAdd: function onAdd(map) {
    this._map = map;
    this._container = L.DomUtil.create("div", "leaflet-control-velocity");
    L.tooltip({}, this._leafletVelocity);
    L.DomEvent.disableClickPropagation(this._container);
    this.options.tooltip ? map.on("mousemove", this._onMouseMove, this) : this._container.style.setProperty("visibility", "hidden");
    if (this.options.popup) map.on("click", this._onClick, this);
    this._container.innerHTML = this.options.emptyString;
    if (this._leafletVelocity.options.onAdd) this._leafletVelocity.options.onAdd();
    return this._container;
  },
  onRemove: function onRemove(map) {
    map.off("mousemove", this._onMouseMove, this);
    map.off("click", this._onClick, this);
    if (this._leafletVelocity.options.onRemove) this._leafletVelocity.options.onRemove();
  },
  vectorToSpeed: function vectorToSpeed(uMs, vMs, unit) {
    var velocityAbs = Math.sqrt(Math.pow(uMs, 2) + Math.pow(vMs, 2));

    function meterSec2Knots(meters) {
      return meters / 0.514;
    }

    function meterSec2kilometerHour(meters) {
      return meters * 3.6;
    } // Default is m/s


    switch (unit) {
      case "km/h":
        return meterSec2kilometerHour(velocityAbs);

      case "kt":
        return meterSec2Knots(velocityAbs);

      default:
        return velocityAbs;
    }
  },
  vectorToDegrees: function vectorToDegrees(uMs, vMs, angleConvention) {
    // Default angle convention is CW
    if (angleConvention.endsWith("CCW")) {
      // vMs comes out upside-down..
      vMs = vMs > 0 ? vMs = -vMs : Math.abs(vMs);
    }

    var velocityAbs = Math.sqrt(Math.pow(uMs, 2) + Math.pow(vMs, 2));
    var velocityDir = Math.atan2(uMs / velocityAbs, vMs / velocityAbs);
    var velocityDirToDegrees = velocityDir * 180 / Math.PI + 180;

    if (angleConvention === "bearingCW" || angleConvention === "meteoCCW") {
      velocityDirToDegrees += 180;
      if (velocityDirToDegrees >= 360) velocityDirToDegrees -= 360;
    }

    return velocityDirToDegrees;
  },
  togglePopup: function togglePopup() {
    this.options.popup = !this.options.popup;
    this.options.popup ? this._map.on("click", this._onClick, this) : this._map.off("click", this._onClick, this);
  },
  enablePopup: function enablePopup() {
    if (this.options.popup === false) {
      this.options.popup = true;

      this._map.on("click", this._onClick, this);
    }
  },
  disablePopup: function disablePopup() {
    if (this.options.popup === true) {
      this.options.popup = false;

      this._map.off("click", this._onClick, this);
    }
  },
  _onClick: function _onClick(e) {
    var _this = this;

    var map = this._map;
    var pos = map.containerPointToLatLng(L.point(e.containerPoint.x, e.containerPoint.y));

    var gridValue = this._leafletVelocity._windy.interpolatePoint(pos.lng, pos.lat);

    var lat = pos.lat.toFixed(6);
    var lng = pos.lng.toFixed(6);

    var buildHtmlContent = function buildHtmlContent() {
      if (gridValue && !isNaN(gridValue[0]) && !isNaN(gridValue[1]) && gridValue[2]) {
        var direction = _this.vectorToDegrees(gridValue[0], gridValue[1], _this.options.angleConvention).toFixed(2) + "&deg";

        var speed = _this.vectorToSpeed(gridValue[0], gridValue[1], _this.options.speedUnit).toFixed(2) + _this.options.speedUnit;

        if (_this.options.setPopupContent) {
          return _this.options.setPopupContent({
            lat: lat,
            lng: lng,
            direction: direction,
            speed: speed
          });
        }

        return "<strong>".concat(_this.options.latLabel, ": </strong>").concat(lat, "\n          <br><strong>").concat(_this.options.lngLabel, ": </strong>").concat(lng, "\n          <br><strong>").concat(_this.options.name, " (").concat(_this.options.directionLabel, "): </strong>").concat(direction, "\n          <br><strong>").concat(_this.options.name, " (").concat(_this.options.speedLabel, "): </strong>").concat(speed);
      }

      return _this.options.emptyString;
    };

    map.openPopup(buildHtmlContent(), pos);
  },
  _onMouseMove: function _onMouseMove(e) {
    var self = this;
    var map = this._map;
    var pos = map.containerPointToLatLng(L.point(e.containerPoint.x, e.containerPoint.y));

    var gridValue = this._leafletVelocity._windy.interpolatePoint(pos.lng, pos.lat);

    var htmlOut = "";

    if (gridValue && !isNaN(gridValue[0]) && !isNaN(gridValue[1]) && gridValue[2]) {
      htmlOut = "<strong>".concat(this.options.name, " (").concat(this.options.directionLabel, "): </strong>\n        ").concat(self.vectorToDegrees(gridValue[0], gridValue[1], this.options.angleConvention).toFixed(2), "&deg,\n        <strong>").concat(this.options.name, " (").concat(this.options.speedLabel, "): </strong>\n        ").concat(self.vectorToSpeed(gridValue[0], gridValue[1], this.options.speedUnit).toFixed(2) + this.options.speedUnit);
    } else {
      htmlOut = this.options.emptyString;
    }

    self._container.innerHTML = htmlOut;
  }
});

L.control.velocity = function (options) {
  return new VelocityControl(options);
};